import React from 'react'
import {motion} from "framer-motion";

export default function Projects() {
    return (
        <>
            <div className="container-fluid mx-auto">
            <div className="row row-cols-1 row-cols-md-2 row-cols-lg-3 g-5 m-lg-5 m-2 pb-5 mt-5 d-flex justify-content-center" >
                        <div className="col" id="animation-card">
                            <motion.div
                            whileHover={{ y: -10 }}
                                className="card h-100 style border-0 rounded shadow"
                                id="animate-image2"
                            >
                                <img
                                    src="https://svgshare.com/i/VD7.svg"
                                    className="card-img-top img-fluid w-75 mx-auto"
                                    alt="..."
                                />
                                <div className="card-body">
                                    <h5 className="card-title">Covid 19 WebApp</h5>
                                    <p className="card-text">
                                        Find all the necessary info about covid 19 here
                                    </p>
                                </div>
                                <div className="card-footer">
                                <a href="https://covid-19infoapp.netlify.app/" target="_blank" rel="noreferrer"><h5 className="btn btn-primary w-50 me-auto rounded">Visit</h5></a>
                                </div>
                            </motion.div>
                        </div>
                        <div className="col">
                            <motion.div
                            whileHover={{ y: -10 }}
                             className="card h-100 style border-0 rounded shadow">
                                <img
                                    src="https://svgshare.com/i/VCk.svg"
                                    className="card-img-top img-fluid w-75 mx-auto"
                                    alt="..."
                                />
                                <div className="card-body">
                                    <h5 className="card-title">To-do App</h5>
                                    <p className="card-text">
                                        Manage your work efficiently with this to-do application.
                                    </p>
                                </div>
                                <div className="card-footer">
                                <a href="https://sauravkr818-todo.netlify.app/" target="_blank" rel="noreferrer"><h5 className="btn btn-primary w-50 me-auto rounded">Visit</h5></a>
                                </div>
                            </motion.div>
                        </div>
                        <div className="col">
                            <motion.div
                            whileHover={{ y: -10 }}
                             className="card h-100 style border-0 rounded shadow">
                                <img
                                    src="https://svgshare.com/i/VCV.svg"
                                    className="card-img-top img-fluid w-75 mx-auto"
                                    alt="..."
                                />
                                <div className="card-body">
                                    <h5 className="card-title">React Admin Panel</h5>
                                    <p className="card-text">
                                        Manage your table efficiently
                                    </p>
                                </div>
                                <div className="card-footer">
                                <a href="http://sauravkumar-adminpanel.netlify.app/" target="_blank" rel="noreferrer"><h5 className="btn btn-primary w-50 me-auto rounded">Visit</h5></a>
                                </div>
                            </motion.div>
                        </div>
                    </div>
            </div>
        </>
    )
}
