import React from "react";
import { NavLink } from "react-router-dom";
import "./Header.css";

export default function Header() {
    
    return (
        <>
            <div className="navbar navbar-expand-lg navbar-light bg-white fixed-top shadow">
                <a className="navbar-brand ms-auto me-auto ps-5" href="#home">
                    <img
                        src="https://i.ibb.co/bz0s6vP/my-img.jpg"
                        className="img-fluid my-auto rounded-circle me-2"
                        alt="..."
                        style={{ width: "30px" }}
                    />
                    Saurav Kumar
                </a>
                <div
                    className="btn navbar-toggler border-0"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarNavDropdown"
                    aria-controls="navbarNavDropdown"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon"></span>
                </div>
                <div
                    className="collapse navbar-collapse justify-content-lg-end ms-sm-2 me-lg-5"
                    id="navbarNavDropdown"
                >
                    <ul className="navbar-nav align-items-center">
                        <li activeClassName="menu_active" className="nav-item">
                            <NavLink
                                activeClassName="menu_active"
                                exact
                                className="nav-link active"
                                aria-current="page"
                                to="/"
                            >
                                {" "}
                                <i
                                    className="fas fa-home pe-1"
                                    style={{ color: "#845ef7" }}
                                ></i>
                                Home
                            </NavLink>
                        </li>
                        <li className="nav-item me-2">
                            <a className="nav-link color" href="/#aboutme">
                                <i
                                    className="fas fa-user pe-1"
                                    style={{ color: "#845ef7" }}
                                ></i>
                                About me
                            </a>
                        </li>
                        <li className="nav-item me-2">
                            <NavLink
                                activeClassName="menu_active"
                                className="nav-link"
                                to="/projects"
                            >
                                <i
                                    className="fas fa-project-diagram pe-1"
                                    style={{ color: "#845ef7" }}
                                ></i>
                                Projects
                            </NavLink>
                        </li>
                        <li className="nav-item me-2">
                            <NavLink
                                // activeClassName="menu_active"
                                className="nav-link"
                                to="/#contact"
                            >
                                <i
                                    className="fas fa-address-card pe-1"
                                    style={{ color: "#845ef7" }}
                                ></i>
                                Contact
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    );
}
